body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  margin:0;
  padding:0;
  width:auto;
  height:auto;
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  font-family: Century Gothic,CenturyGothic,AppleGothic,sans-serif;
}


.Frame{
    margin:30px 50px 30px 50px;
    padding:30px;
    background: #ffffb3;
    box-shadow: 0 0 42px -3px rgba(0,0,0,0.75);
    border-radius: 5%;
    position:relative;
    max-width:800px;
    width:80%;
}

.tasksList{
padding:0;
}

.log-out{
    position: absolute;
    right:5px;
    top:5px;
    padding: 5px;
    color: white;
    background: #ff4d4d;
    border-radius: 50%;
    border: 1px #ffffff solid;
    font-family: 'Indie Flower', cursive;
    font-size: 15px;
    font-weight: bold;
    letter-spacing: 1.5px;
    outline: none;
}

.log-out:hover{
    cursor: pointer;
    background: #ff332c;
}

.log-out:active{
    background: #ff151a;
}

@media (min-width: 560px) {
    .Frame{
        width:auto;
    }
}

@font-face {
    font-family: "Indie Flower";
    src: url(/bestNotes/static/media/IndieFlower.cbd50882.ttf);
}

.FrameTitle h1{
    text-transform: uppercase;
    font-family: 'Indie Flower', cursive;
    font-size:50px;
    margin:10px;
}


.FrameTitle h3{
    margin:5px;
    font-size:20px;
}

.FrameTitle h3::before{
    content:"<<<<<<<\B7\B7\B7\B7\B7\B7>>>>>>>";
    display: block;
    font-size:25px;
    -webkit-transform: scaleX(0.5) scaleY(1.5);
    transform: scaleX(0.5) scaleY(1.5);
    margin-bottom:15px;
    font-weight: normal;

}

@media (min-width: 640px) {
    .FrameTitle h3{
        margin:5px;
        font-size:20px;
    }

    .FrameTitle h3::after, .FrameTitle h3::before{
        display: inline-block;
        letter-spacing: -5px;
        font-size:25px;
        -webkit-transform: scaleX(0.5) scaleY(1.5);
        transform: scaleX(0.5) scaleY(1.5);
        margin-bottom:15px;
        font-weight: normal;

    }

    .FrameTitle h3::after{
        content:">>>>>>>>>>";
    }

    .FrameTitle h3::before{
        content:"<<<<<<<<<<";
    }
}



@font-face {
    font-family: "Indie Flower";
    src: url(/bestNotes/static/media/IndieFlower.cbd50882.ttf);
}

.AddButton {
    position: absolute;
    right: 10px;
    bottom: 10px;
    padding: 5px;
    color: white;
    background: #ffb3b3;
    border-radius: 50%;
    border: 1px #ffffff solid;
    font-family: 'Indie Flower', cursive;
    font-size: 15px;
    font-weight: bold;
    letter-spacing: 1.5px;
    outline: none;
}


.AddButton:hover{
    background: #ff8080;
    cursor: pointer;
}

.AddButton:active{
    background: #ff4d4d;
}

.Form{
    margin-top:30px;
    list-style: none;
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content:  center;
}

.Form label{
    color:#b8b894;
    font-weight: bold;
    font-size:20px;
    margin-right:15px;
}
.Form input[type=text]{
    font-weight: 500;
    font-size:18px;
    border:none;
    border-bottom: #c2c2a3 1.5px solid ;
    background: inherit;
    cursor:pointer;
    width:auto;
    color:#b8b894;
}
.Form input[type=text]:focus{
    cursor: auto;
}

.ThingToDo{
    list-style: none;
    display:flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
}

input[type=checkbox] {
    display: none;
}
@-webkit-keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@-webkit-keyframes fadeOut {
    from { opacity: 1; }
    to { opacity: 0; }
}
@keyframes fadeOut {
    from { opacity: 1; }
    to { opacity: 0; }
}

.ThingToDo label {
    display: inline-block;
    cursor: pointer;
    width:100%;
    text-align: left;
    text-indent: 10px;
    font-weight: 500;
    font-size:18px;
    margin:5px;
    line-height: 150%; /* Safari */
    transition: color 1s ;
    transition-timing-function: ease-out;
    -webkit-animation: fadeIn 1s;
    animation: fadeIn 1s;
    overflow-wrap: break-word;
}

.ThingToDo label::after{
    content:"";
    border: 1px solid #c2c2a3;
    display: block;
    width:100%;

}

.ThingToDo .customCheck{
    min-width: 18px;
    height:18px;
    padding-bottom: 5px;
    background:white;
    border:0.5px #c2c2a3 solid;
    cursor:pointer;
}
.ThingToDo .customCheck:hover{
    background:#f5f5f0;
}

