@font-face {
    font-family: "Indie Flower";
    src: url(/bestNotes/static/media/IndieFlower.cbd50882.ttf);
}

.LoginForm{
    margin-left:10px;
    margin-right:10px;
    margin-top: 50px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.LoginForm label{

