    margin-bottom: 20px;
}

.LoginForm input[type="submit"]{
    padding: 15px;
    color: white;
    background: #ffb3b3;
    border-radius: 50%;
    border: 1px #ffffff solid;
    font-family: 'Indie Flower', cursive;
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1.5px;
}

.LoginForm input[type="submit"]:hover{
    background: #ff8080;
    cursor: pointer;
}

.LoginForm input[type="submit"]:active{
    background: #ff4d4d;
}

